<template>
  <div class="xscan-code">
    <p style="margin-top: 200px; color: red" @click="todoBack()">123</p>
    <qrcode-stream
      v-show="isShow"
      :camera="camera"
      :torch="torch"
      @decode="onDecode"
      @init="onInit"
    >
      <i class="xicon icon-arrow-left"></i>
      <p class="xscan-code-line"></p>
      <p class="xscan-code-tip" :class="{ error: error }">
        {{ error || tipMessage }}
      </p>
    </qrcode-stream>
  </div>
</template>

<script>
import { QrcodeStream } from 'vue-qrcode-reader'

export default {
  name: 'scanCode',
  components: { QrcodeStream },
  data() {
    return {
      isShow: false,
      error: ''
    }
  },
  props: {
    camera: {
      type: String,
      default: 'rear'
    },
    torch: {
      type: Boolean,
      default: false
    },
    tipMessage: {
      type: String,
      default: ''
    }
  },
  methods: {
    /**
     * 
     */
    onDecode(result) {
      console.log('', result)
      this.$emit('decodeEvent', result)
    },
    /**
     * 
     */
    async onInit(promise) {
      try {
        await promise
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          this.error = 'ERROR: '
        } else if (error.name === 'NotFoundError') {
          this.error = 'ERROR: '
        } else if (error.name === 'NotSupportedError') {
          this.error = 'ERROR: (HTTPS)'
        } else if (error.name === 'NotReadableError') {
          this.error = 'ERROR: '
        } else if (error.name === 'OverconstrainedError') {
          this.error = 'ERROR: '
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.error = 'ERROR: API'
        } else if (error.name === 'InsecureContextError') {
          this.error = 'ERROR: HTTP'
        } else {
          this.error = error.name
        }
        this.$emit('errorEvent', this.error, error)
      }
    },
    /**
     * 
     */
    todoBack() {
      this.isShow = !this.isShow
      this.$emit('backEvent')
    }
  }
}
</script>

<style>
.xicon {
  display: inline-block;
  background-size: 100% 100%;
}
.xicon.icon-arrow-left {
  width: 0.2rem;
  height: 0.32rem;
  background-image: url(./images/arrow_left_white.png);
}
.xscan-code {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
.xscan-code .xicon {
  position: absolute;
  top: 0.28rem;
  left: 0.3rem;
}
.xscan-code-line {
  margin-left: 5%;
  width: 90%;
  height: 0.06rem;
  box-shadow: 0 0 2rem #02e0fe;
  background: url(./images/scan-line.png) no-repeat center/ 100% 100%;
  animation: scan-move 4s linear infinite;
}
.xscan-code-tip {
  position: absolute;
  top: 80vh;
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  font-size: 0.28rem;
  line-height: 0.39rem;
}
.xscan-code-tip.error {
  color: red;
}
@keyframes scan-move {
  0% {
    transform: translateY(20vh);
    opacity: 1;
  }
  50% {
    transform: translateY(50vh);
    opacity: 1;
  }
  100% {
    transform: translateY(80vh);
    opacity: 0;
  }
}
</style>
